<template>
  <div>
    <s-table  
      ref="table"
      :bordered="false"
      :autoLoad="false"
      :data="loadData"
      size="small"
      rowKey="code"
    >
      <a-table-column 
        :title="this.$t('task.index1')"
        align="center"
        width="50px"
      >
        <span slot-scope="text">
          <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
        </span>
      </a-table-column>
      <a-table-column
        :title="this.$t('task.plan_cdate')"
        dataIndex="cdate"
        align="left"
        width="170px" 
      >
        <span slot-scope="text,record">
          <pick-label :defaultValue="record.cdate" />
        </span>
      </a-table-column>
      <!-- <a-table-column
        :title="this.$t('task.plan_code')"
        dataIndex="code"
      ></a-table-column> -->
      <a-table-column
        :title="this.$t('task.plan_exectime')"
        dataIndex="execTime"
        align="left"
        width="170px" 
      >
        <span slot-scope="text,record">
          <pick-label :defaultValue="record.execTime" />
        </span>
      </a-table-column>
      <a-table-column
        :title="this.$t('task.plan_datatime')"
        dataIndex="dataTime"
        align="left"
        width="170px" 
      >
        <span slot-scope="text,record">
          <pick-label :defaultValue="record.dataTime" />
        </span>
      </a-table-column>
      
      <a-table-column
        :title="this.$t('task.plan_status')"
        dataIndex="status"
        align="left"
        width="170px" 
        :customRender="status => !status?'':this.$refs.planStatus.getLabel(status)"
      ></a-table-column>
      <a-table-column
        :title="this.$t('task.action')"
        align="left"
        
        dataIndex="action"
      > 
        <span slot-scope="text,record">
          <btn-edit @click="onReset(record)" type="a" :text="$t('task.plan_reset')"></btn-edit>
          <a-divider type="vertical"/>
          <btn-detail @click="onDetail(record)" type="a" :text="$t('task.plan_detail')"></btn-detail>
        </span></a-table-column>
    </s-table>
    <dict type="TASK_PLAN_STATUS" ref="planStatus" v-show="false" :isPublic="true"/>
    <record-list ref="recordTable" />
  </div>
</template>
<script>
  import STable from '@/components/table/'
  import Dict from '@/views/public/Dict'
  import { axios } from '@/utils/request'
  import RecordList from '@/views/sys/task/RecordList'
  export default {
    name: 'Index',
    components: {
      STable,
      Dict,
      RecordList
    },
    data () {
      return {    
        queryParam: {},     
        loadData: parameter => {          
          return axios({
            url: '/sys/task/search-list-plan',
            method: 'get',
            params: Object.assign(parameter, this.queryParam)
          }).then(res => { 
            return res.result
          })
        }
      }
    },
    methods: {
      init(code){
        if(this.queryParam.taskCode != code){
          this.queryParam.taskCode = code
          this.$refs.table.refresh()
        }
      },
      onDetail(record){
        this.$refs.recordTable.showModal(record.code)
      },
      onReset(record){
        const that = this
        that.$confirm({
          title: that.$t('task.plan_msg1'),
          content: that.$t('task.plan_msg2'),
          okType: 'danger',
          onOk() {
            axios({
              url: '/sys/task/update-reset',
              method: 'post',
              params: {code: record.code}
            }).then(result=>{
              that.loading = false
              if(result){
                this.$emit('ok',result)
              }
            })
          }
        })
      }
    }
  }
</script>
