<template>
  <div>
    <div :spinning="loading">
      <a-card class="card" :title="$t('task.edit_card')" :bordered="false">
        <a-form :form="form" class="form">
          <a-form-item
            :label="$t('task.task_code')"
            v-bind="formItemLayout"
          >
            <a-input
              style="width: 220px"
              :placeholder="$t('task.code_placeholder')"
              :disabled="params.opType!='1'"
              v-decorator="[
                'code',
                {initialValue: data.code,rules: [{required: true,message: $t('task.code_msg1')}]}
              ]"
            />
          </a-form-item> 
          <a-form-item        
            :label="$t('task.task_name')"
            v-bind="formItemLayout"
          >
            <a-input 
              :placeholder="$t('task.task_name_placeholder')"
              v-decorator="[
                'description',
                {initialValue: data.description,rules: [{required: true,message: $t('task.description_msg1')}]}
              ]"
            />
          </a-form-item> 
          <a-form-item        
            :label="$t('task.type')"
            v-bind="formItemLayout"
          >
            <dict 
              :isPublic="true"
              style="width: 220px"
              type="TASK_TYPE"
              v-model="taskType"
              @change="taskTypeSelect"
              v-decorator="[
                'type',
                {initialValue: [data.type],rules: [{required: true,message: $t('task.type_msg1'),validator: (rule, value, callback)=>value&&value[0]?callback():callback($t('task.type_msg1')) }]}
              ]"
            ></dict>

          </a-form-item>
          <a-form-item
            :label="$t('task.work_cycle')"
            v-bind="formItemLayout"
          >
            <a-input
              type="number"
              style="width: 220px"
              :placeholder="$t('task.cycle_placeholder')"
              v-decorator="[
                'cycle',
                {initialValue: data.cycle,rules: [{required: true,message: $t('task.cycle_msg1')}]}
              ]"
            >
              <dict 
                :isPublic="true"
                style="width: 80px"
                slot="addonAfter"
                type="REPEAT_TYPE"
                v-model="cycleType"
                @change="cycleTypeSelect"
                v-decorator="[
                  'cycleType',
                  {initialValue: [data.cycleType]}
                ]"
              ></dict>
            </a-input>
            
          </a-form-item>
          
          <a-form-item
            :label="$t('task.base_time')"
            v-bind="formItemLayout"
          >

            <pick-time 
              style="width: 220px !important;display: inline-block;" 
              :defaultValue="data.baseTime" 
              @change="time=>{data.baseTime = time}"
              v-decorator="[
                'baseTime',
                {initialValue: data.baseTime,rules: [{required: true,message: $t('task.base_time_msg1')}]}
              ]"
            />
          </a-form-item>
          <a-form-item
            :label="$t('task.next_time')"
            v-bind="formItemLayout"
          >
            <pick-time 
              style="width: 220px !important;display: inline-block;" 
              :defaultValue="data.nextTime" 
              @change="time=>{data.nextTime = time}"
              v-decorator="[
                'nextTime',
                {initialValue: data.nextTime,rules: [{required: true,message: $t('task.next_time_msg1')}]}
              ]" 
            />
          </a-form-item>
          <a-form-item
            :label="$t('task.edit_work_id')"
            v-bind="formItemLayout"
          >
            <a-input 
              :placeholder="$t('task.ws_id_placeholder')"
              v-decorator="[
                'wsId',
                {initialValue: data.wsId,rules: [{required: true,message: $t('task.ws_id_msg1')}]}
              ]"
            />
          </a-form-item> 
          <a-form-item
            :label="$t('task.params')"
            v-bind="formItemLayout"
          >
            <a-textarea 
              rows="10" 
              :placeholder="$t('task.params_placeholder')"
              v-decorator="[
                'params',
                {initialValue: data.params}
              ]"
            />
          </a-form-item>
        </a-form>
      </a-card>
    </div>
    <footer-tool-bar :style="{ width: '100%'}">
      <a-button @click="onCancel">{{ $t('common.btn_cancel') }}</a-button>&nbsp;&nbsp;
      <a-button @click="onSubmit" type="primary" v-if="params.opType!='3'">{{ $t('common.btn_ok') }}</a-button>
    </footer-tool-bar>
  </div>
</template>
<script>
  import { axios } from '@/utils/request'
  import Dict from '@/views/public/Dict'
  import FooterToolBar from '@/components/FooterToolbar'
  import notification from 'ant-design-vue/es/notification'

  export default {
    components: {
      Dict,
      FooterToolBar,
      notification
    },
    data() {
      return {
        taskType:'',
        cycleType:'',
        loading: false,
        form: this.$form.createForm(this),
        data: {},
        formItemLayout: {labelCol: {lg: {span: 7}, sm: {span: 7}},wrapperCol: {lg: {span: 14}, sm: {span: 14}}}
      }
    },
    props: {
      params: {
        type: Object,
        default: null
      }
    },
    mounted(){
      this.initModel()
    },
    methods: {
      taskTypeSelect(e){
        e?this.taskType = e[0]:this.taskType = ''
      },
      cycleTypeSelect(e){
        e?this.cycleType=e[0]:this.cycleType=''
      },
      initModel(){
        if(this.params.opType=='2' || this.params.opType=='3'){
          this.initData()
        }
      },
      initData(){
        const that = this
        that.loading = true
        axios({
          url: that.params=='2'?'/sys/task/update-load':'/sys/task/detail',
          method: 'get',
          params: {code: that.params.code}
        }).then(response=>{
          that.loading = false
          that.data = response.result
          that.cycleType=[that.data.cycleType]
        })
      },
      onSubmit(){
        this.form.validateFields((err,values) => {
          if (!err) {            
            const that = this
            that.data = Object.assign(that.data,values)
            if(that.data.cycleType){
              that.data.cycleType = that.data.cycleType[0]
            }
            that.data.type = that.data.type[0]
            if(!that.params){
              return
            }
            if (that.cycleType=='') {
              const arr = [{
                message: that.$t('task.cycle_type_msg1'),
                field: 'cycle',
              }]
              this.form.setFields({ cycle: {errors: arr } })
              return
            }
            that.loading = true
            axios({
              url: that.params.opType=='1'?'/sys/task/insert':'/sys/task/update',
              method: 'post',
              params: that.data
            }).then(result=>{
              that.loading = false
              if(result){
                this.$emit('ok',result)
              }
            })
          }
        })
      },
      onCancel(){
        this.$emit('cancel')
      },
    }
  }
</script>
<style lang="scss" scoped>
  .card{
    margin-bottom: 24px;
  }
</style>
