<template>
  <a-spin :spinning="loading" :tip="$t('task.tip')" size="large">
    <div :bordered="false" v-show="!isEdit">
      <transition name="fade">
        <a-card style="background-color: #fff8db;margin-bottom: 10px;" v-show="show">
          <div class="table-page-search-wrapper">
            <a-form v-if="reset_flag" layout="inline">
              <a-row :gutter="48">
                <a-col :md="6" :sm="24">
                  <a-form-item 
                    :label="$t('task.task_code')"
                  >
                    <a-input v-model="queryParam.code" :placeholder="$t('task.code_placeholder')"/>
                  </a-form-item>
                </a-col>
                <a-col :md="6" :sm="24">
                  <a-form-item 
                    :label="$t('task.task_name')"
                  >
                    <a-input v-model="queryParam.description" :placeholder="$t('task.task_name_placeholder')"/>
                  </a-form-item>
                </a-col> 
                <!-- <a-col :md="6" :sm="24">
                  <a-form-item 
                    :label="$t('task.description')"
                  >
                    <a-input v-model="queryParam.description" :placeholder="$t('task.description_placeholder')"/>
                  </a-form-item>
                </a-col>  -->
                <a-col :md="6" :sm="24">
                  <a-form-item 
                    :label="$t('task.actived')"
                  >
                    <dict 
                      :isPublic="true"
                      type="TASK_ACTIVED" 
                      ref="taskActived" 
                      @change="(value)=>{value?queryParam.actived=value[0]:delete queryParam.actived}"
                    ></dict>
                  </a-form-item>
                </a-col> 
                <a-col :md="6" :sm="24">
                  <btn-search @click="onSearch" :showInput="false"></btn-search>
                  <a-button @click="onReset" style="margin-left: 8px">{{ $t('common.btn_reset') }}</a-button>
                </a-col>
              </a-row>
            </a-form>
          </div>
        </a-card>
      </transition>
      <a-card>
        <div class="table-page-search-wrapper" style="margin-bottom:10px;">
          <a-row>
            <a-col :md="19" :sm="19">
              <btn-add @click="onAdd" type="bt" :text="$t('task.add_task')"></btn-add>
            </a-col>
            <a-col :md="5" :sm="5">
              <sorter @refresh="onSearch" @show="showSearch"></sorter>
            </a-col>
          </a-row>
        </div>
        <s-table  
          ref="table"
          :bordered="false"
          :autoLoad="true"
          :data="loadData"
          size="small"
          rowKey="code"
          @onClick="record=>{$refs.planTable.init(record.code)}"
        >
          <a-table-column 
            :title="this.$t('task.index1')"
            align="center"
            width="50px"
          >
            <span slot-scope="text">
              <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
            </span>
          </a-table-column>
          <a-table-column
            :title="this.$t('task.code')"
            dataIndex="code"
            align="left"
            width="60px" 
          ></a-table-column>
          <a-table-column
            :title="this.$t('task.task_name')"
        
            dataIndex="description"
            align="left"
            width="280px" 
          ></a-table-column>
          <a-table-column
            :title="this.$t('task.work_id')"
            dataIndex="wsId"
            align="left"
            width="140px" 
          ></a-table-column>
          <a-table-column
            :title="this.$t('task.type')"
            dataIndex="type"
            align="left"
            width="100px" 
            :customRender="type => !type?'':this.$refs.taskType.getLabel(type)"
          ></a-table-column>
          <a-table-column
            :title="this.$t('task.work_cycle')"
            dataIndex="cycle"
            align="left"
            width="130px" 
          >
            <span slot-scope="text,record">
              {{ record.cycle + " " + $refs.cycleType.getLabel(record.cycleType) }}
            </span>
          </a-table-column>
          <!-- <a-table-column
            :title="this.$t('task.sn')"
            align="right"
            dataIndex="sn"
          ></a-table-column> -->
          <a-table-column
            :title="this.$t('task.base_time')"
            dataIndex="baseTime"
            align="left"
            width="160px" 
          >
            <span slot-scope="text,record">
              <pick-label :defaultValue="record.baseTime" />
            </span>
          </a-table-column>
          
          <a-table-column
            :title="this.$t('task.next_time')"
            dataIndex="nextTime"
            align="left"
            width="160px" 
          >
            <span slot-scope="text,record">
              <pick-label :defaultValue="record.nextTime" />
            </span>
          </a-table-column>
          <a-table-column
            :title="this.$t('task.actived')"
            dataIndex="actived"
            align="left"
            width="80px" 
          >
            <span slot-scope="text,record">
              <btn-switch 
                :defaultChecked="record.actived=='Y'"
                :checkedChildren="$refs.taskActived.getLabel('Y')"
                :unCheckedChildren="$refs.taskActived.getLabel('N')"
                @change="checked => onSwitch(checked, record)"
              ></btn-switch> 
            </span>
          </a-table-column>
          <a-table-column
            :title="this.$t('task.action')"
            align="left"
            dataIndex="action"
          > 
            <span slot-scope="text,record">
              <btn-edit @click="onUpdate(record)" type="a"></btn-edit>
              <a-divider type="vertical"/>
              <btn-remove @click="onRemove(record)" type="a"></btn-remove>
              <!-- <a-dropdown>
                <a class="ant-dropdown-link">
                  {{ $t('common.btn_more') }} <a-icon type="down" />
                </a>
                <a-menu slot="overlay">
                  <a-menu-item>
                    <btn-detail @click="onDetail(record)" type="b"></btn-detail>
                  </a-menu-item>
                  <a-menu-item>
                    <btn-remove @click="onRemove(record)" type="b"></btn-remove>
                  </a-menu-item>
                </a-menu>
              </a-dropdown> -->
            </span>
          </a-table-column>
        </s-table>
        <dict type="REPEAT_TYPE" ref="cycleType" v-show="false" :isPublic="true"/>
        <dict type="TASK_TYPE" ref="taskType" v-show="false" :isPublic="true"/>
      </a-card>
      <a-card style="margin-top: 8px;" :title="$t('task.task_plan_list')">
        <plan-list ref="planTable" />
      </a-card>
    </div> 
    <task-edit 
      v-if="isEdit"
      @ok="onSubmit" 
      @cancel="onCancel" 
      :params="editParams"
    ></task-edit>
  </a-spin>
</template>

<script>
  import STable from '@/components/table/'
  import TaskEdit from '@/views/sys/task/TaskEdit'
  import PlanList from '@/views/sys/task/PlanList'
  import Dict from '@/views/public/Dict'
  import { axios } from '@/utils/request'
  export default {
    name: 'Index',
    components: {
      STable,
      TaskEdit,
      PlanList,
      Dict,
    },
    data () {
      return { 
        loading: false,
        reset_flag: true,
        isEdit: false,
        show: true,
        editParams: {},       
        queryParam: {},     
        loadData: parameter => {          
             return axios({
              url: '/sys/task/search-list-task',
              method: 'get',
              params: Object.assign(parameter, this.queryParam)
            }).then(res => { 
              this.$refs.planTable.init('')
              return res.result
            })
        }
      }
    },
    methods: {
      onReset(){
        const that = this
        that.queryParam = {}
        that.reset_flag = false
        this.$nextTick(() => {
          that.reset_flag = true
        })
      },
      onSearch() {
        this.$refs.table.refresh()
      },
      showSearch() {
        this.show = !this.show
      },
      onAdd() {
        this.editParams = {opType: '1'}
        this.isEdit = true
      },
      onUpdate (record) {       
        this.editParams = {opType: '2',code: record.code}
        this.isEdit = true  
      },
      onDetail(record){
        this.editParams = {opType: '3',code: record.code}
        this.isEdit = true
      },
      onSwitch(checked, record){
        const that = this
        axios({
          url: '/sys/task/switch',
          method: 'post',
          params: {code: record.code, actived: checked?'Y':'N'}
        }).then(()=>{
          that.$refs.table.refresh()
        })
      },
      onRemove (record) {
        var _this = this
        axios({
          method: 'delete',
          url: '/sys/task/delete',
          params: {code: record.code}
        }).then(function () {            
          _this.$refs.table.refresh()
        })
      },
      onSubmit(){
        this.isEdit = false
        this.$refs.table.refresh()
      },
      onCancel(){
        this.isEdit=false
      }
    }
  }
</script>
<style lang="scss">
.error {
  color: red;
}
.normal {
  color: #00660f;
}
// .icon {
//   width: 25px;
//   height: 25px;
// }
</style>