<template>
  <a-modal
    :maskClosable="false" 
    :body-style="{ padding: '10px' }" 
    :title="$t('task.record_list')"
    :width="800"
    v-model="visible"
    :cancelText="$t('common.btn_cancel1')"
  > 
    <template slot="footer">
      <a-button key="back" @click="handleCancel">
        {{ $t("task.close") }}
      </a-button>
    </template>
    <div class="table-page-search-wrapper">
      <s-table
        ref="table"
        :bordered="false"
        :autoLoad="true"
        :data="loadData"
        style="max-height: 500px;overflow-y: auto"  
        size="small"     
        rowKey="sn"        
      >
        <a-table-column 
          :title="this.$t('task.index1')"
          align="center"
          width="50px"
        >
          <span slot-scope="text">
            <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
          </span>
        </a-table-column>
        <!-- <a-table-column
          :title="this.$t('task.record_sn')"
          dataIndex="sn"
        ></a-table-column> -->
        <a-table-column
          :title="this.$t('task.record_stime')"
          dataIndex="startTime"
          align="left"
          width="140px" 
        >
          <span slot-scope="text,record" :class="record.result == 'S' ? '': 'error'">
            <pick-label :defaultValue="record.startTime" />
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('task.record_etime')"
          dataIndex="endTime"
          align="left"
          width="140px" 
        >
          <span slot-scope="text,record" :class="record.result == 'S' ? '': 'error'">
            <pick-label :defaultValue="record.endTime" />
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('task.record_result')"
          dataIndex="result"
          align="left"
          width="140px" 
        >
          <span slot-scope="text,record" :class="record.result == 'S' ? '': 'error'">
            <span v-if="text=='S'">{{ $t("task.success") }}</span>
            <span v-else>{{ $t("task.fail") }}</span>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('task.record_remark')"
          dataIndex="remark"
          align="left"
        >
          <span slot-scope="text,record" :class="record.result == 'S' ? '': 'error'">
            {{ text }}
          </span>
        </a-table-column>
      </s-table>
    </div>
  </a-modal>
</template>
<script>
import STable from '@/components/table/'
import { axios } from '@/utils/request'
export default {
  components: {
    STable
  },
  data () {
    return {
      visible: false,
      queryParam: {},
      loadData: (parameter) => {
        return axios({
          url: '/sys/task/search-list-record',
          method: 'get',
          params: Object.assign(parameter, this.queryParam)
        }).then(data=>{ 
          return data.result
        })
      }
    }
  },
  methods: {
    showModal(code) {
      this.visible = true
      this.queryParam.code = code
      this.$refs.table.refresh()
    },
    handleCancel() {
      this.visible = false
    },
  },
}
</script>
